  src: url("../../fonts/bebas-neue-bold.woff2") format("woff2"), url("../../fonts/bebas-neue-bold.woff") format("woff"); }

.container {
  max-width: 1160px;
  margin: 0 auto;
  box-sizing: border-box; }
  @media (max-width: 1199px) {
    .container {
      max-width: 100%;
      margin: 0 45px; } }
  @media (max-width: 767px) {
    .container {
      margin: 0 15px; } }

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.6px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "roboto-condensed", sans-serif;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  box-sizing: border-box; }
  .btn--primary {
    border-width: 4px; }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1; }
  .header__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 31px; }
    @media (max-width: 1199px) {
      .header__wrap {
        padding-top: 40px; } }
    @media (max-width: 767px) {
      .header__wrap {
        padding-top: 20px; } }
  .header__item {
    font-size: 26px;
    line-height: 1;
    letter-spacing: 0.8px;
    font-weight: bold;
    text-decoration: none;
    font-family: "bebas-neue", sans-serif;
    color: #FFFFFF; }
    @media (max-width: 1199px) {
      .header__item {
        font-size: 24px;
        letter-spacing: 0.5px; } }
    @media (max-width: 767px) {
      .header__item {
        font-size: 20px;
        letter-spacing: 0.4px; } }
    .header__item--logo {
      letter-spacing: 0.5px; }
      @media (max-width: 767px) {
        .header__item--logo {
          letter-spacing: 0.4px; } }

.hero {
  color: #FFFFFF;
  background-size: cover;
  background: #003D85 url("../img/hero-bg.jpg") no-repeat center center; }
  .hero__wrap {
    display: flex;
    align-items: flex-end;
    padding: 51px 0; }
    @media (max-width: 1199px) {
      .hero__wrap {
        justify-content: center;
        width: 100%;
        margin: 0;
        padding: 82px 0; } }
    @media (max-width: 767px) {
      .hero__wrap {
        padding: 115px 0 78px; } }
  .hero__desc {
    width: 397px;
    margin-bottom: 43px;
    text-align: right; }
    @media (max-width: 1199px) {
      .hero__desc {
        width: 326px;
        margin-bottom: 23px; } }
    @media (max-width: 767px) {
      .hero__desc {
        width: 294px;
        margin-bottom: 0; } }
  .hero__city {
    display: block;
    margin: 0 1px 20px 0;
    font-size: 49px;
    line-height: 1;
    letter-spacing: 1.5px;
    font-family: "bebas-neue", sans-serif; }
    @media (max-width: 1199px) {
      .hero__city {
        margin-bottom: 8px;
        font-size: 34px; } }
    @media (max-width: 767px) {
      .hero__city {
        margin-bottom: 14px;
        font-size: 28px;
        letter-spacing: 1px; } }
  .hero__image {
    margin-left: 63px; }
    @media (max-width: 1199px) {
      .hero__image {
        margin-left: 28px; } }
    @media (max-width: 767px) {
      .hero__image {
        display: none; } }
    .hero__image img {
      display: block; }
  .hero__btn {
    width: 234px;
    height: 61px; }
    @media (max-width: 1199px) {
      .hero__btn {
        width: 187px; } }
    @media (max-width: 767px) {
      .hero__btn {
        width: 100%;
        height: 63px; } }

.hero-title {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  margin: 0 1px 50px 0;
  font-size: 111px;
  line-height: 109px;
  letter-spacing: 2.6px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: "bebas-neue", sans-serif; }
  @media (max-width: 1199px) {
    .hero-title {
      margin-bottom: 35px;
      font-size: 91px;
      line-height: 86px;
      letter-spacing: 1px; } }
  @media (max-width: 767px) {
    .hero-title {
      align-items: flex-start;
      margin-bottom: 38px;
      font-size: 83px;
      line-height: 77px;
      letter-spacing: 0.8px; } }
  .hero-title__first {
    width: 100%; }
  @media (max-width: 767px) {
    .hero-title__second {
      padding-top: 1px; } }
  .hero-title__desc {
    margin-right: 31px;
    padding-bottom: 21px;
    font-size: 14px;
    line-height: 1.5;
    letter-spacing: normal;
    font-weight: normal;
    text-transform: none;
    font-family: "roboto-condensed", sans-serif; }
    @media (max-width: 1199px) {
      .hero-title__desc {
        margin-right: 27px;
        font-size: 14px; } }
    @media (max-width: 767px) {
      .hero-title__desc {
        margin-right: 10px;
        padding: 0; } }

.hero-social {
  margin: 0 0 42px auto;
  list-style: none; }
  @media (max-width: 1199px) {
    .hero-social {
      display: none; } }
  .hero-social li:not(:last-child) {
    margin-bottom: 19px; }
  .hero-social__link {
    display: block;
    font-size: 12px;
    line-height: 14px;
    text-decoration: none;
    text-align: right;
    font-family: "roboto-condensed", sans-serif;
    color: #FFFFFF; }
